/* Carousel */

.carousel { -webkit-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.35); box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.35); }

.carousel .item {
  overflow:hidden;
  width: 100%;
  height: 75vh;
}

.carousel-caption {
  bottom: 7%;
  text-shadow: 0 1px 2px rgba(0,0,0,.8);
}

.bannerImg {
  max-width: 100%;
  position: absolute;
  bottom: 0;
}

.carousel-indicators, .carousel-control, .glyphicon { visibility: hidden; }

.btn-success {
  margin-top: 50px;
  padding: 15px 30px;
  font-size: 24px;
}

.btn-success:hover { background-color: #419641; }

/* Overview */

.Overview { padding: 45px 0; }

.OvImg { width: 375px; }

.Overview h2 { margin-bottom: 20px; }

.OvTxt {
  text-align: left;
  margin-top: 5%;
}

/* Footer */

.Footer {
  background-color: #3a3a3a;
  margin: 0;
  padding: 15px;
}

.Footer li {
  display: inline;
  list-style: none;
  margin: 30px;
  font-weight: 100;
}

.Footer a {
  display: inline;
  color: #b7b7b7;
}



